.login {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    background-color: RGB(31, 34, 75);
}

.loginText {
    font-size: 32px;
}

.content {
    border-radius: 10px;
    padding: 10px 30px;
    width: 452px;
    background-color: white;
    margin-top: 100px;
}

.footer {
    text-align: center;
    margin: 24px;
}

.logFoot {
    margin-right: 10px;
    margin-bottom: 15px;
    margin-left: 50px;
}

.minHeight {
    min-height: 30px;
}
